import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
//import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Meropriyatia from './panels/meropriyatia';
import Raspisanie from './panels/raspisanie';
import PD from './panels/pd/PD';
import PSA from './panels/psa/PSA';
import Y from './panels/y/Y';
import PD197 from './panels/pd/PD-197';
import PD198 from './panels/pd/PD-198';
import PD199 from './panels/pd/PD-199';
import PD298 from './panels/pd/PD-298';
import PD298a from './panels/pd/PD-298a';
import PD299 from './panels/pd/PD-299';
import Raspd197 from './panels/pd/raspisanie/raspd197';


const App = () => {
	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	//const [popout, setPopout] = useState(<ScreenSpinner size='large' />);

	useEffect(() => {
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_black';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
			setUser(user);
		}
		fetchData();
	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	return (
		<View activePanel={activePanel}>
			<Home id='home' fetchedUser={fetchedUser} go={go} />
			<Raspisanie id='raspisanie' go={go} />
			<Meropriyatia id='meropriyatia' go={go} />
			<PD id='pd' go={go} />
			<PSA id='psa' go={go} />
			<Y id='y' go={go} />
			<PD197 id='pd-197' go={go} />
			<PD198 id='pd-198' go={go} />
			<PD199 id='pd-199' go={go} />
			<PD298 id='pd-298' go={go} />
			<PD298a id='pd-298a' go={go} />
			<PD299 id='pd-299' go={go} />
			<Raspd197 id='raspd197' go={go} />
		</View>
	);
}

export default App;

